<template>
  <div class="intro">
    <h1>Hello <span>World</span></h1>
    <p>{{ desc }}</p>
  </div>
</template>

<script>
export default {
  name: 'Intro',
  data () {
    return {
      desc: 'I\'m Joe. I build user centric interfaces.'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$main: #4ECDC4;
$accent: #FF6B6B;
$grey: #7f8c8d;
.intro{
  padding: 2em 0em 4em;
  h1 {
    font-size: 5em;
    line-height: 1em;
    margin: 1em 0 .25em;
    color: $main;
    span{
      color: $accent;
    }
  }
  p{
    color: $grey;
    font-size: 1em;
    line-height: 1.75em;
  }
}

</style>



