<template>
  <div class="work-item">
    <div class="work-image">
      <div>
        <img src="@/assets/browser-cap.png" alt="Browser Cap">
        <video v-if="item.video" video playsinline autoplay loop muted>
          <source :src="getPic(item.video)" type="video/mp4">
          <img v-if="item.image" :src="getPic(item.image)" />
        </video>
        <img v-else :src="getPic(item.image)" />
      </div>
    </div>
    <div class="work-text">
      <div>
        <h2>{{ item.title }}</h2>
        <h5>
          <span v-for="(program, index) in item.programs">
          	{{program}}<template v-if="index != (item.programs.length - 1)"> / </template>
          </span>
        </h5>
        <p>{{ item.desc }}</p>
        <a v-if="item.link" :href="item.link" target="_blank" rel="noopener noreferrer">View Site</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WorkItems',
  methods: {
    getPic(img) {
      return '/static/' + img;
    }
  },
  props: ['item']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$main: #4ECDC4;
$accent: #FF6B6B;
$grey: #7f8c8d;
$sm: 500px;
$md: 767px;
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
.work-item{
  font-family: 'Montserrat', sans-serif;
  @media (min-width: $md){
    display: flex;
    padding: 3em 0;
  }
  & > [class*='work-']{
    width: 100%;
    padding: 1em;
    @media (min-width: $md){
      padding: 4em 2em;
      flex-basis: 50%;
      width:50%;
    }
  }
  &:nth-child(even){
    .work-image{
      order: 2;
    }
    .work-text{
      order: 1;
    }
  }
  .work-text{
    text-align: left;
    color: $grey;
    @media (max-width: $md){
      padding: 1em 3em 3em;
    }
    @media (max-width: $sm){
      padding: 1.5em 1.5em 2em;
    }
    @media (min-width: $md){
      display: flex;
      align-items: center;
    }
    h2{
      margin-top: 0;
    }
    h5{
      color: $main;
      margin: 0;
    }
    p{
      font-size: 1em;
      line-height: 1.75em;
    }
    a{
      color:$main;
      &:hover{
        color:$accent;
      }
    }
  }
  .work-image{
    text-align: left;
    @media (max-width: $md){
      padding: 3em;
    }
    @media (max-width: $sm){
      padding: 1.5em;
    }
    & > div{
        box-shadow: 0px 12px 30px rgba(142, 142, 142, 0.5);
    }
    img{
      width: 100%;
      display: block;
    }
     video{
      width: 100%;
      display: block;
    }
  }
}

</style>



