<template>
  <div class="contact">
    <div>
      <h1><span class="main">Drop Me</span> <span class="accent"><br>a <a href="mailto:joseph.clarkson20@gmail.com">Line</a></span></h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Intro',
  data () {
    return {
      desc: 'I\'m Joe. I build user centric interfaces'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$main: #4ECDC4;
$accent: #FF6B6B;
.contact{
  padding: 40px;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  h1{
    text-align: right;
    font-size: 5em;
    margin: 1em 0px .25em;
    .main{
      color: $main;
    }
    .accent{
      color: $accent;
    }
  }
  a{
    color:$accent;
    &:hover{
      color:$main;
    }
  }
}

</style>



