<template>
  <div class="work-items">
    <WorkItems v-for="(item,index) of items" v-bind:item="item"/>
  </div>
</template>

<script>
import WorkItems from './WorkItems'
import workData from '../json/work.json'
export default {
  name: 'Work',
  components: {
    WorkItems
  },
  data () {
    return {
      items: workData
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.work-items{
  margin: 0 auto;
  max-width: 1260px;
}
</style>



